import React from "react"
import addToMailchimp from 'gatsby-plugin-mailchimp'

class Newsletter extends React.Component {
    state = {
        email: '',
        showMessage: false,
        messageClassName: ''
    }

    _handleChange = (e) => {
        this.setState({
            [`${e.target.name}`]: e.target.value,
        });
    }

    _handleSubmit = (e) => {
        e.preventDefault();

        var self = this;
        addToMailchimp(this.state.email, this.state)
            .then(({ msg, result }) => {
                if (result !== 'success') {
                    throw msg;
                }

                self.setState({
                    email: '',
                    showMessage: true,
                    message: msg,
                    messageClassName: 'text-center alert alert-success'
                });
            })
            .catch((err) => {
                self.setState({
                    showMessage: true,
                    message: err,
                    messageClassName: 'text-center alert alert-warning'
                });
            });
    }

    render() {
        return (
            <div className="container">
                <div className="text-center">
                    <h3 className="text-center">Subscribe to our mailing list</h3>
                    <p className="lead">Sign up and don't miss our awesome Mage Mastery video tutorials</p>

                    <div className="mt-4">
                        { this.state.showMessage && <div className={this.state.messageClassName} dangerouslySetInnerHTML={{ __html: this.state.message}} />}
                        <div className="mx-auto form-container">
                            <form onSubmit={this._handleSubmit}>
                                <div className="input-group mb-3 subscribe-input-group">
                                    <input type="email"
                                           name="email"
                                           onChange={this._handleChange}
                                           className="form-control"
                                           aria-describedby="button-addon2"
                                           placeholder="Your email here..."
                                           value={this.state.email}
                                    />
                                    <div className="input-group-append">
                                        <button className="btn btn-outline-secondary" type="button" id="button-addon2" onClick={this._handleSubmit}>Subscribe</button>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Newsletter
