import React from 'react'

import Layout from "../components/layout"
import SEO from "../components/seo"
import Link from 'gatsby-link'
import {graphql} from "gatsby"
import '../styles/lesson.scss'
import Newsletter from '../components/newsletter'

import '../styles/lesson.scss'

export default function Template({data}) {
    const {markdownRemark: lesson} = data;

    const title = lesson.frontmatter.title

    return (
        <Layout>
            <SEO title={ title } />
            <div className="courses-header pb-5 pt-5">
                <div className="container">
                    <h1 className="mb-0">{lesson.frontmatter.course_title}</h1>
                </div>
            </div>
            <div className="container mb-5 mt-0">
                <div className="content-container pt-5 pb-2">
                    <div dangerouslySetInnerHTML={{__html: lesson.html}} />
                </div>
                <div className="jumbotron jumbotron-fluid mb-0">
                    <Newsletter />
                </div>
            </div>
            <div className="navbar lesson-nav">
                <div className="container">
                    <div className="col">
                        {lesson.frontmatter.prev !== '' ? 
                          <Link to={lesson.frontmatter.prev} className="btn btn-block text-right btn-left" role="button" aria-pressed="true">
                              Previous Lesson
                          </Link> : 
                          <div className="text-right">Previous Lesson</div>
                        }
                    </div>
                    <div className="col">
                        {0 && lesson.frontmatter.next !== '' ?
                          <Link to={lesson.frontmatter.next} className="btn btn-block btn-right text-left" role="button" aria-pressed="true">
                            Next Lesson
                          </Link> : 
                          <div>Next Lesson</div>
                        }
                    </div>
                </div>
            </div>
        </Layout>
    )
}

export const pageQuery = graphql`
    query LessonByPath($path: String!) {
      markdownRemark(
        frontmatter: { 
          path: { eq: $path } 
        }
      ) {
        html
        frontmatter {
          path
          title
          course_title
          next
          prev
        }
      }
    }
`
